import modelExtend from 'dva-model-extend'
import { queryUserLists, freezeUser } from '../../services/users'
import { message } from 'antd'
import { pageModel } from '../../models/common'
import queryString from 'query-string'
import { getLocalStorage } from '../../utils/utils'

interface Istate {
  currentItem: any,
  modalVisible: boolean,
}

export default modelExtend(pageModel, {
  namespace: 'userlists',
  state: {
    currentItem: {},
    modalVisible: false,
  },
  subscriptions: {
    setup ({ dispatch, history }: any) {
      history.listen((location: any) => {
        if (location.pathname === '/users/userlists') {
          dispatch({
            type: 'query',
            payload: {
              // status: 2,
              ...queryString.parse(location.search),
            },
          })
        }
      })
    },
  },
  effects: {
    * query ({ payload }: any, { call, put, select }: any) {
      const searchValue = yield select((state: any) => state.userlists.searchValue)
      const data = yield call(queryUserLists, Object.assign(payload, { name: searchValue }))
      if (data.success) {
        const userProfile = data.data.user_profile
        yield put({
          type: 'querySuccess',
          payload: {
            list: userProfile.data,
            pagination: {
              current: Number(payload.current_page) || 1,
              pageSize: Number(payload.per_page) || 10,
              total: userProfile.total,
            },
          },
        })
      } else {
        throw data
      }
    },
    * freezeUser ({ payload }: any, { call, put }: any) {
      const userId = getLocalStorage('user_id')
      const data = yield call(freezeUser, { ...payload }, { user_id: userId })
      if (data.success) {
        message.success('封禁成功')
        yield put({
          type: 'query',
          payload: {
          },
        })
      }
    },
  },
  reducers: {
    updateState (state: Istate, { payload }: any) {
      return {
        ...state,
        ...payload,
      }
    },
  },
})



