import config from '../utils/config'
import request from '../utils/request'

const { api } = config
const {
  userlists, complaintList, freezeuserlist, freezeuser,
} = api

export function queryUserLists (params: any) {
  return request({
    url: userlists,
    method: 'get',
    data: params,
  })
}
export function queryFreezeUser (params: any) {
  return request({
    url: freezeuserlist,
    method: 'get',
    data: params,
  })
}
export function freezeUser (params: any, users: any) {
  const url = freezeuser.replace(':uid', users.user_id)
  return request({
    url: url.replace(':freezeid', params.freezeid),
    method: 'put',
    data: params,
  })
}

export function queryComplaint (params: any) {
  return request({
    url: complaintList,
    method: 'get',
    data: params,
  })
}
